	module.exports = {"data":{"markdownRemark":{"html":"<p>All vulnerability reports submitted to your program are sent directly to the inbox where you can view and manage your reports.</p>\n<p>The inbox contains these different features to help you manage your reports:</p>\n<ul>\n<li><a href=\"inbox-views.html\">Inbox views</a></li>\n<li>Filters</li>\n<li>Search</li>\n<li><a href=\"response-labels.html\">Response labels</a> (only if your program doesn't meet response targets or standards)</li>\n</ul>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/inbox-44f799def82ceedd679928fbb412a72d-a3b63.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 50.29991431019708%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"inbox\"\n        title=\"\"\n        src=\"/static/inbox-44f799def82ceedd679928fbb412a72d-2f183.png\"\n        srcset=\"/static/inbox-44f799def82ceedd679928fbb412a72d-67829.png 125w,\n/static/inbox-44f799def82ceedd679928fbb412a72d-5c59f.png 250w,\n/static/inbox-44f799def82ceedd679928fbb412a72d-2f183.png 500w,\n/static/inbox-44f799def82ceedd679928fbb412a72d-c93e8.png 750w,\n/static/inbox-44f799def82ceedd679928fbb412a72d-4e628.png 1000w,\n/static/inbox-44f799def82ceedd679928fbb412a72d-b3ac4.png 1500w,\n/static/inbox-44f799def82ceedd679928fbb412a72d-a3b63.png 2334w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>You can customize your filters and inbox views to better fit the workflow for your program.</p>\n<h3>Navigating the Inbox</h3>\n<p>To navigate through your inbox:</p>\n<ol>\n<li>Click <b>Inbox</b> in your header to access your inbox.</li>\n<li>Search for your reports by using the:</li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Search bar</td>\n<td>Type the name or keywords of a report to search for relevant reports.</td>\n</tr>\n<tr>\n<td>Filters</td>\n<td>Click \n<b>\nShow filters\n</b>\n to view the different options you can select to filter your reports by.</td>\n</tr>\n<tr>\n<td><a href=\"inbox-views.html\">Inbox Views</a></td>\n<td>These are saved filters that categorize your inbox reports.</td>\n</tr>\n</tbody>\n</table>\n<ol start=\"3\">\n<li>Click the report you want to view .</li>\n<li><a href=\"report-actions.html\">Take action</a> on the report.</li>\n</ol>\n<p>There are <a href=\"keyboard-shortcuts.html\">keyboard shortcuts</a> you can use to help you to quickly navigate through your reports in the inbox.</p>","frontmatter":{"path":"/programs/inbox.html","id":"programs/parent-child-programs","title":"Inbox","description":null}}},"pathContext":{}}

